/* Lato */
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2");
  font-display: swap;
}
/* Oswald */
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  display: block;
  height: auto;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.page__body {
  min-width: 320px;
  font-family: "Lato", sans-serif;
  font-size: 14px;
  line-height: 18px;
  background-color: #ffffff;
  font-weight: 400;
  color: #000000;
  margin: 0;
}

.page__content-container {
  position: relative;
  width: 320px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page__content-container {
    width: 768px;
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .page__content-container {
    width: 1440px;
    padding: 0 110px;
  }
}

.title {
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  line-height: 40px;
  color: inherit;
  font-weight: 400;
  margin: 0;
}
@media (min-width: 768px) {
  .title {
    font-size: 60px;
    line-height: 60px;
  }
}

.logo {
  display: block;
}
.logo img,
.logo svg {
  display: block;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 40px;
  padding: 7px 44px;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  color: #ffffff;
  background-color: #68b738;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .button {
    height: 40px;
    padding: 14px 26px;
    font-size: 20px;
    line-height: 20px;
  }
}
.button:hover:not(:disabled) {
  background-color: #5eaa2f;
}
.button:active:not(:disabled) {
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  opacity: 0.3;
  cursor: default;
}
.button--secondary {
  background-color: #f2f2f2;
  color: #444444;
}
.button--secondary:hover:not(:disabled) {
  background-color: #ebebeb;
}
.button--secondary:active:not(:disabled) {
  color: rgba(68, 68, 68, 0.3);
}

.food-type {
  padding-bottom: 20px;
}
@media (min-width: 768px) {
  .food-type {
    padding-bottom: 45px;
  }
}
@media (min-width: 1440px) {
  .food-type {
    padding-bottom: 71px;
  }
}

.food-type__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 1440px) {
  .food-type__list {
    display: flex;
    flex-wrap: wrap;
    column-gap: 80px;
    row-gap: 30px;
  }
}

.food-type__item {
  position: relative;
  background-color: #f2f2f2;
  padding: 30px 33px 18px 20px;
}
@media (min-width: 768px) {
  .food-type__item {
    padding: 77px 62px 53px 52px;
  }
}
@media (min-width: 1440px) {
  .food-type__item {
    width: 570px;
    padding: 77px 61px 55px 52px;
    margin: 0;
  }
}
.food-type__item:not(:last-child) {
  margin-bottom: 20px;
}
@media (min-width: 1440px) {
  .food-type__item:not(:last-child) {
    margin-bottom: 0;
  }
}
.food-type__item::before {
  content: "";
  position: absolute;
  display: block;
}
.food-type__item--slim::before {
  top: 21px;
  width: 50px;
  height: 53px;
  background: url("../img/icon/cat-slim.svg") no-repeat center center;
  background-size: cover;
}
@media (max-width: 767px) {
  .food-type__item--slim::before {
    left: 22px;
  }
}
@media (min-width: 768px) {
  .food-type__item--slim::before {
    top: 81px;
    right: 62px;
    width: 200px;
    height: 211px;
  }
}
@media (min-width: 1440px) {
  .food-type__item--slim::before {
    top: 41px;
    left: 52px;
    width: 100px;
    height: 106px;
  }
}
.food-type__item--pro::before {
  top: 24px;
  width: 67px;
  height: 50px;
  background: url("../img/icon/cat-pro.svg") no-repeat center center;
  background-size: cover;
}
@media (max-width: 767px) {
  .food-type__item--pro::before {
    left: 13px;
  }
}
@media (min-width: 768px) {
  .food-type__item--pro::before {
    top: 91px;
    right: 28px;
    width: 268px;
    height: 200px;
  }
}
@media (min-width: 1440px) {
  .food-type__item--pro::before {
    top: 47px;
    left: 33px;
    width: 134px;
    height: 100px;
  }
}

.food-type__title {
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  padding-left: 69px;
  margin-bottom: 29px;
}
@media (min-width: 768px) {
  .food-type__title {
    font-size: 36px;
    line-height: 36px;
    padding-left: 0;
    padding-right: 254px;
    margin-bottom: 24px;
  }
}
@media (min-width: 1440px) {
  .food-type__title {
    padding-left: 162px;
    padding-right: 0;
    margin-bottom: 73px;
  }
}

.food-type__description {
  margin: 0;
  margin-bottom: 38px;
}
@media (min-width: 768px) {
  .food-type__description {
    font-size: 16px;
    line-height: 24px;
    padding-right: 254px;
    margin-bottom: 48px;
  }
}
@media (min-width: 1440px) {
  .food-type__description {
    padding-right: 0;
    margin-bottom: 23px;
  }
}

.food-type__button {
  display: inline-block;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #000000;
  text-align: center;
  padding: 4px 47px 4px 0;
  text-decoration: none;
  background: url("../img/icon/button-arrow.svg") no-repeat 98px center;
}
@media (min-width: 768px) {
  .food-type__button {
    font-size: 20px;
    line-height: 30px;
    padding: 4px 57px 4px 0;
    background: url("../img/icon/button-arrow.svg") no-repeat 129px center;
  }
}
@media (min-width: 1440px) {
  .food-type__button {
    padding: 4px 60px 4px 0;
    background: url("../img/icon/button-arrow.svg") no-repeat 133px center;
  }
}
.food-type__button:active {
  opacity: 0.3;
}

.example {
  position: relative;
  padding: 25px 0 40px 0;
}
@media (min-width: 768px) {
  .example {
    padding-bottom: 100px;
  }
}
@media (min-width: 1440px) {
  .example {
    padding-bottom: 75px;
  }
}
.example::before {
  content: "";
  position: absolute;
  top: -43px;
  left: 0;
  display: none;
  height: 1px;
  width: 100%;
  background-color: #e6e6e6;
}
@media (min-width: 768px) {
  .example::before {
    display: block;
  }
}
@media (min-width: 1440px) {
  .example::before {
    display: none;
  }
}
.example::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  height: 100%;
  width: 100%;
  background-color: #eaeaea;
  z-index: -1;
}
@media (min-width: 768px) {
  .example::after {
    top: 100%;
    height: 462px;
    transform: translateY(-100%);
  }
}

@media (min-width: 1440px) {
  .example__main-container {
    display: flex;
  }
}

@media (min-width: 1440px) {
  .example__container {
    width: 436px;
    flex-grow: 1;
    margin-right: 224px;
  }
}

.example__title {
  margin-bottom: 40px;
}
@media (min-width: 768px) {
  .example__title {
    margin-bottom: 63px;
  }
}
@media (min-width: 1440px) {
  .example__title {
    margin-bottom: 146px;
  }
}

.example__description {
  color: #444444;
  margin: 0;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .example__description {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 54px;
  }
}
@media (min-width: 1440px) {
  .example__description {
    margin-bottom: 69px;
  }
}

@media (min-width: 768px) {
  .example__wrapper {
    display: flex;
    justify-content: space-between;
    margin-bottom: 104px;
  }
}
@media (min-width: 1440px) {
  .example__wrapper {
    display: block;
    margin-bottom: 0;
  }
}

.example__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  margin-bottom: 34px;
}
@media (min-width: 768px) {
  .example__list {
    margin-bottom: 0;
  }
}
@media (min-width: 1440px) {
  .example__list {
    justify-content: stretch;
    margin-bottom: 60px;
  }
}

.example__item {
  position: relative;
  width: 124px;
  padding: 15px 20px 18px;
  border: 1px solid #cdcdcd;
}
@media (min-width: 768px) {
  .example__item {
    width: 162px;
    padding: 15px 30px 20px;
  }
}
@media (min-width: 768px) {
  .example__item:nth-child(2n+1) {
    margin-right: 42px;
  }
}

.example__achievement {
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  line-height: 24px;
  text-align: center;
}
@media (min-width: 768px) {
  .example__achievement {
    font-size: 30px;
    line-height: 37px;
  }
}

.example__achievement-description {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 50%);
  font-family: "Lato", sans-serif;
  font-size: 12px;
  line-height: 12px;
  color: #444444;
  background-color: #eaeaea;
  text-align: center;
  margin: 0;
}
@media (min-width: 768px) {
  .example__achievement-description {
    min-width: 125px;
    font-size: 14px;
    line-height: 14px;
    background-color: #ffffff;
  }
}
@media (min-width: 1440px) {
  .example__achievement-description {
    background-color: #eaeaea;
  }
}

.example__total {
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  .example__total br {
    display: none;
  }
}
@media (min-width: 768px) {
  .example__total {
    font-size: 20px;
    line-height: 28px;
    text-align: right;
    margin-bottom: 0;
  }
}
@media (min-width: 1440px) {
  .example__total {
    text-align: left;
  }
  .example__total br {
    display: none;
  }
}

.main-footer {
  background-color: #f2f2f2;
  padding-bottom: 40px;
}
@media (min-width: 768px) {
  .main-footer {
    padding-bottom: 55px;
  }
}
@media (min-width: 1440px) {
  .main-footer {
    padding-bottom: 66px;
  }
}

.main-footer__container {
  margin-bottom: 40px;
}
@media (min-width: 768px) {
  .main-footer__container {
    margin-bottom: 55px;
  }
}
@media (min-width: 1440px) {
  .main-footer__container {
    margin-bottom: 66px;
  }
}

@media (min-width: 768px) {
  .main-footer_wrapper {
    display: flex;
    justify-content: space-between;
