  font-size: 16px;
  line-height: 19px;
  color: #444444;
  text-decoration: none;
}
.main-footer__developer-logo:hover svg, .main-footer__developer-logo:focus svg {
  color: #68b738;
}
.main-footer__developer-logo:active {
