}
@media (min-width: 768px) {
  .main-footer__developer-logo span {
    margin-right: 28px;
  }
}

@media (min-width: 1440px) {
  .address__content-container {
    position: relative;
    width: 1440px;
    margin: 0 auto;
  }
}

.address__container {
  background-color: #f2f2f2;
  padding: 26px 20px;
}
@media (min-width: 768px) {
  .address__container {
    padding: 56px 70px;
  }
}
@media (min-width: 1440px) {
  .address__container {
    position: absolute;
    top: 95px;
    left: 110px;
    width: 565px;
    background-color: #ffffff;
    padding: 56px 60px 56px 80px;
  }
}
.address__container--secondary {
