@media (min-width: 768px) {
  .address__building,
.address__city {
    width: 160px;
    font-size: 16px;
    line-height: 20px;
  }
}
@media (min-width: 1440px) {
  .address__building,
.address__city {
    width: 175px;
  }
}

@media (min-width: 768px) {
  .address__building {
    margin-bottom: 20px;
  }
}

.address__wrapper {
  display: flex;
  justify-content: space-between;
}

.address iframe {
  border: none;
  display: block;
  width: 100vw;
}
@media (min-width: 768px) {
  .address iframe {
    height: 400px;
  }
}

.product-card {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 11px 0 14px 0;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .product-card {
    display: block;
    width: 324px;
    padding: 161px 36px 39px;
    background-color: #f2f2f2;
  }
}
@media (min-width: 1440px) {
  .product-card {
    width: 245px;
    padding: 162px 32px 40px;
  }
}
.product-card--secondary {
  display: block;
  padding: 70px 0 0 0;
}
@media (min-width: 768px) {
  .product-card--secondary {
    display: flex;
    flex-direction: column;
    background-color: transparent;
    border: 2px solid #f2f2f2;
    padding: 161px 38px 38px;
  }
}
@media (min-width: 1440px) {
  .product-card--secondary {
    padding: 160px 38px 41px;
  }
}
.product-card--secondary .product-card__plus-image {
  position: absolute;
  top: 17px;
  left: 50%;
  width: 30px;
  height: 30px;
  transform: translateX(-50%);
  background: url("../img/icon/plus.svg") no-repeat center;
  background-size: cover;
}
@media (min-width: 768px) {
  .product-card--secondary .product-card__plus-image {
    top: 52px;
    width: 60px;
    height: 60px;
  }
}
@media (min-width: 768px) {
  .product-card--secondary .product-card__plus-image {
    top: 51px;
  }
}
.product-card--secondary .product-card__title {
  text-align: center;
  padding-right: 0;
  margin-bottom: 8px;
}
@media (min-width: 768px) {
  .product-card--secondary .product-card__title {
    padding: 0 50px;
    margin-bottom: 23px;
  }
}
@media (min-width: 1440px) {
  .product-card--secondary .product-card__title {
    padding: 0 0;
  }
}
.product-card--secondary .product-card__description:not(:last-child) {
  display: block;
  text-align: center;
  margin-bottom: 21px;
}
@media (min-width: 768px) {
  .product-card--secondary .product-card__description:not(:last-child) {
    padding: 0 40px;
    margin-bottom: 0;
  }
}
@media (min-width: 1440px) {
  .product-card--secondary .product-card__description:not(:last-child) {
    padding: 0 0;
  }
}
@media (min-width: 768px) {
  .product-card--secondary .product-card__button {
    margin-top: auto;
  }
}
.product-card--secondary .product-card__description::after {
  display: none;
}

.product-card__image {
  grid-column: 1/2;
}
@media (min-width: 768px) {
  .product-card__image {
    position: absolute;
    top: -73px;
    left: 50%;
    width: 168px;
    height: 200px;
    transform: translateX(-50%);
  }
}
@media (min-width: 1440px) {
  .product-card__image {
    top: -80px;
    width: 172px;
    height: 196px;
  }
}

.product-card__container {
  grid-column: 2/3;
}

.product-card__title {
  font-size: 16px;
  line-height: 20px;
  color: #222222;
  text-transform: uppercase;
  padding-right: 44px;
  margin-bottom: 18px;
}
@media (min-width: 768px) {
  .product-card__title {
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    padding: 0 50px;
    margin-bottom: 21px;
  }
}
@media (min-width: 1440px) {
  .product-card__title {
    font-size: 20px;
    line-height: 21px;
    padding: 0 25px;
  }
}

.product-card__description {
  position: relative;
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
  margin: 0;
  margin-bottom: 3px;
}
@media (min-width: 768px) {
  .product-card__description {
    font-size: 16px;
  }
}
.product-card__description:not(:last-child) {
  margin-bottom: 3px;
}
@media (min-width: 768px) {
  .product-card__description:not(:last-child) {
    margin-bottom: 14px;
  }
}
@media (min-width: 1440px) {
  .product-card__description:not(:last-child) {
    margin-bottom: 15px;
  }
}
.product-card__description:last-child {
  margin-bottom: 22px;
}
@media (min-width: 768px) {
  .product-card__description:last-child {
    margin-bottom: 42px;
  }
}
.product-card__description::after {
  content: "";
  position: absolute;
  bottom: -9px;
  left: 0;
  display: none;
  width: 100%;
