.product-card__button {
  grid-column: 1/-1;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 26px;
}

.additional-offer {
  padding-bottom: 35px;
}
@media (min-width: 768px) {
  .additional-offer {
    padding-bottom: 0;
  }
}
@media (min-width: 1440px) {
  .additional-offer {
    width: 245px;
