  background: linear-gradient(to top, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/background-image/additional-offer.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  padding: 188px 61px 44px 57px;
}
@media (min-width: 768px) {
  .additional-offer__container {
    padding: 71px 124px 69px 422px;
    background-position: center center, 0 -316px;
  }
}
@media (min-width: 1440px) {
  .additional-offer__container {
    padding: 185px 42px 42px;
    background-position: center center, -34px 0;
  }
}
@media (min-resolution: 2dppx) {
  .additional-offer__container {
    background: linear-gradient(to top, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/background-image/additional-offer@2x.jpg");
    background-position: center center, 0 -294px;
    background-size: cover;
  }
}
.additional-offer__container::before {
  content: "";
  position: absolute;
  top: 59px;
  left: 50%;
  display: block;
  width: 78px;
  height: 80px;
  background: url("../img/icon/gift.svg");
  background-size: cover;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .additional-offer__container::before {
    top: 57px;
